  createBranch({
    commit,
  }, branch) {
    return new Promise((resolve, reject) => {
      console.log(branch)

      let branchData = new FormData()
      branchData.append('name', branch.Name)
      branchData.append('state', branch.State.name)
      branchData.append('region', branch.Region)
      branchData.append('countryCode', branch.CountryCode)
      branchData.append('phoneNumber', branch.PhoneNumber)
      branchData.append('email', branch.Email)
      branchData.append('countryId', branch.countryId._id)
      branchData.append('address', branch.Address)
      branchData.append('logo', branch.Logo)
      branchData.append('zone', branch.Zone)
      branchData.append('pastor', branch.Pastor)


      let userToken = localStorage.getItem('token');
      axios({
          url: '/admin/branch',
          data: branchData,
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
            'x-auth-token': userToken,
            'publicToken': branch.publicToken
          }
        })
        .then(resp => {
          resolve(resp);
        })
        .catch(err => {
          if (err.response) {
            if (err.response.status === 401) {
              dispatch("logout")
            }
          }
          reject(err);
        });
    });
  },
